    import mInput from '../../components/m-input.vue';
	import {  
	        mapMutations  
	    } from 'vuex';

    export default {
        components: {
            mInput
        },
        data() {
            return {
                current:0,
				phone:'',
				auth:'',
				times:'',
				time:0,
				pwd:''
            }
        },
        methods: {
            shortcut() {
				if(this.current !== 0){
					this.current = 0
				}
			},
			topwd() {
				if(this.current !== 1){
					this.current = 1
				}
			},
			toReg() {
				uni.navigateTo({
					url:"../reg/reg"
				})
			},
			toForgetpwd() {
				uni.navigateTo({
					url:"../forgetpwd/forgetpwd"
				})
			},
			loginGetAuth(){
				var p = this.phone;
				var t = /^1[3456789]\d{9}$/;
				if(!(t.test(p))){
					uni.showToast({
						title:"手机号格式不正确",
						icon: "none"
					})
					return
				}else{
					var data = {
						mobile: p,
						send_type:'login_confirm'
					}
					/*
					this.request.httpRequest('api/sms/sendCode',data,'POST').then(res => {
						if (res.data.code === 1000){
							uni.showToast({
								title: res.data.msg,
								icon: "none"
							})
							this.time = 1;
							this.times = 59;
							setInterval(()=>{
								this.times --;
								if(this.times < 1){
									this.time = 2;
									clearInterval()
								}
							},1000)
						}
						if (res.data.code !== 1000){
							uni.showToast({
								title: res.data.msg,
								icon: "none"
							})
						}
					})
					*/
					uni.request({
						url:"http://www.zgkayida.com/api/sms/sendCode",
						data:data,
						header:{
							'content-type': 'application/x-www-form-urlencoded;application/json;charset=UTF-8'
						},
						method:"POST",
						success: (res) => {
							if (res.data.code === 1000){
								uni.showToast({
									title: "发送成功",
									icon: "none"
								})
								this.time = 1;
								this.times = 59;
								setInterval(()=>{
									this.times --;
									if(this.times < 1){
										this.time = 2;
										clearInterval()
									}
								},1000)
							}
							if (res.data.code !== 1000){
								uni.showToast({
									title: res.data.msg,
									icon: "none"
								})
							}
						}
					})
				}
			},
			quickLogin() {
				var p = this.phone;
				var auth = this.auth;
				var data = {
					mobile: p,
					code: auth
				}
				/*
				this.request.httpRequest('api/user/quickLogin',data,'POST').then(res => {
					if (res.data.code !== 1000){
						uni.showToast({
							title: res.data.msg,
							icon: "none"
						})
					}else if(res.data.code === 1000){
						// res.data.retval[mobile] = data.mobile
						var userInfo = res.data.retval
						this.login(userInfo)
						this.whetherbind(res.data.retval.is_bind)
						uni.switchTab({
							url:"../main/main"
						})
					}
				})
				*/
				uni.request({
					url:"http://www.zgkayida.com/api/user/quickLogin",
					data:data,
					header:{
						'content-type': 'application/x-www-form-urlencoded;application/json;charset=UTF-8'
					},
					method:"POST",
					success: (res) => {
						if (res.data.code !== 1000){
							uni.showToast({
								title: res.data.msg,
								icon: "none"
							})
						}else if(res.data.code === 1000){
							var userInfo = res.data.retval
							this.login(userInfo)
							this.whetherbind(res.data.retval.is_bind)
							uni.showToast({
								title:"登录成功",
								icon:"none"
							})
							setTimeout(() => {
								uni.switchTab({
									url:"../main/main"
								})
							},1000)
						}
					}
				})
			},
			pwdLogin() {
				var p = this.phone;
				var pwd = this.pwd;
				var data = {
					mobile: p,
					password: pwd
				}
				/*
				this.request.httpRequest('api/user/login',data,'POST').then(res => {
					if (res.data.code !== 1000){
						uni.showToast({
							title: res.data.msg,
							icon: "none"
						})
					}else if(res.data.code === 1000){
						function extend(target, source) { 
							for (var obj in source) {
								target[obj] = source[obj];
							} 
							return target;
						}
						var userInfo = extend(res.data.retval,data)
						this.login(userInfo)
						this.whetherbind(res.data.retval.is_bind)
						uni.switchTab({
							url:"../main/main"
						})
					}
				})
				*/
				uni.request({
					url:"http://www.zgkayida.com/api/user/login",
					data:data,
					header:{
						'content-type': 'application/x-www-form-urlencoded;application/json;charset=UTF-8'
					},
					method:"POST",
					success: (res) => {
						if (res.data.code !== 1000){
							uni.showToast({
								title: res.data.msg,
								icon: "none"
							})
						}else if(res.data.code === 1000){
							console.log(res.data)
							function extend(target, source) { 
								for (var obj in source) {
									target[obj] = source[obj];
								} 
								return target;
							}
							var userInfo = extend(res.data.retval,data)
							this.login(userInfo)
							this.whetherbind(res.data.retval.is_bind)
							uni.showToast({
								title:"登录成功",
								icon:"none"
							})
							setTimeout(() => {
								uni.switchTab({
									url:"../main/main"
								})
							},1000)
						}
					}
				})
			},
			weixinLogin() {
				uni.getProvider({
					service: 'oauth',
					success: function(res) {
						if (~res.provider.indexOf('weixin')) {
							uni.login({
								provider: 'weixin',
								success: function(loginRes) {
									// 获取用户信息
									uni.getUserInfo({
										provider: 'weixin',
										success: function(infoRes) {
											let data = {
												unionid: infoRes.userInfo.unionId,
												openid: infoRes.userInfo.openId,
												headimgurl: infoRes.userInfo.avatarUrl,
												nickname: infoRes.userInfo.nickName
											}
											this.request.httpRequest('api/user/wechatLogin',data,'POST').then(res => {
												
												if (res.data.code !== 1000){
													uni.showToast({
														title: res.data.msg,
														icon: "none"
													})
												}else if(res.data.code === 1000){
													function extend(target, source) { 
														for (var obj in source) {
															target[obj] = source[obj];
														} 
														return target;
													}
													var userInfo = extend(res.data.retval,data)
													this.login(userInfo)
													this.whetherbind(res.data.retval.is_bind)
													uni.showToast({
														title:"登录成功",
														icon:"none"
													})
													setTimeout(() => {
														uni.switchTab({
															url:"../main/main"
														})
													},1000)
												}
											})
										}.bind(this)
									});
								}.bind(this)
							});
						}
					}.bind(this)
				});
			},
			...mapMutations(['login','whetherbind']),
			userAgreement() {
				uni.navigateTo({
					url:"../userareement/userareement"
				})
			}
        }
    }
